import { NgModule, Component } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FreeTrialComponent } from '../sales-automation-home/free-trial/free-trial.component';
import { PricingComponent } from '../sales-automation-home/pricing/pricing.component';
import { OneMonthProductComponent } from '../sales-automation-home/one-month-product/one-month-product.component';
import { RazorpayPaymentComponent } from './razorpay-payment/razorpay-payment.component';
 
const routes: Routes = [
  {
    path: 'price',
    component:PricingComponent
  }, {
    path: 'payment/:partnerId/:cart',
    component:RazorpayPaymentComponent
  },  {
    path: 'one-month',
    component:OneMonthProductComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MainLayoutRoutingModule {}
