import { ChangeDetectorRef, NgModule } from '@angular/core';
import { MainLayoutRoutingModule } from './main-layout-routing.module';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import {
  FaIconLibrary,
  FontAwesomeModule,
} from '@fortawesome/angular-fontawesome';
import { ActivatedRoute } from '@angular/router';
import { AppCommonModule } from '../common/common.module';
import { NgxCaptchaModule } from 'ngx-captcha';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { MainLayoutComponent } from './main-layout.component';
import { MainFooterComponent } from './footer/footer.component';
import { MainHeaderComponent } from './header/header.component';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import {
  faCheckSquare,
  faRssSquare,
  faSquare,
  faPlus,
  faMinus,
  faTrash,
  faWindowClose,
  faTimes,
  faCheck,
} from '@fortawesome/free-solid-svg-icons';
import { RazorpayPaymentComponent } from './razorpay-payment/razorpay-payment.component';

@NgModule({
  declarations: [MainLayoutComponent, MainFooterComponent, MainHeaderComponent, RazorpayPaymentComponent],
  imports: [
    FormsModule,
    NgxCaptchaModule,
    CommonModule,
    BsDatepickerModule.forRoot(),
    NgbModule,
    BsDropdownModule.forRoot(),
    FontAwesomeModule,
    AppCommonModule,
    MainLayoutRoutingModule,
  ],
  exports: [AppCommonModule],
})
export class MainLayoutModule {
  partnerId: any;
  constructor(private library: FaIconLibrary, private router: ActivatedRoute) {
    library.addIcons(
      faSquare,
      faCheckSquare,
      faRssSquare,
      faPlus,
      faCheckSquare,
      faMinus,
      faTrash,
      faCheck,
      faWindowClose,
      faTimes
    );
    this.partnerId = this.router.snapshot.paramMap.get('pId');
  }
}
