import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Cart } from 'src/app/common/razorpay-checkout/shared/payment.model';

@Component({
  selector: 'app-razorpay-payment',
  templateUrl: './razorpay-payment.component.html',
  styleUrls: ['./razorpay-payment.component.scss'],
})
export class RazorpayPaymentComponent implements OnInit {
  partnerId: any;
  cart: any;
  cartList: Cart[];
  constructor(private route: ActivatedRoute) {
    this.partnerId = this.route.snapshot.paramMap.get('partnerId');
    if(this.partnerId =='null')
    {
      this.partnerId =''
    }
    this.cart = this.route.snapshot.paramMap.get('cart');
    this.cartList = []
  }

  ngOnInit(): void {
    if(this.cart)
    {
      this.cartList =JSON.parse(atob(this.cart)) as Cart[];
    }
  }
}
